var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"txt c-333 fs14 pt30"},[_vm._ssrNode("<h3 class=\"fBold c-333 title fs24\" data-v-03f06b7e>\n    《费用结算服务协议》\n  </h3> <p data-v-03f06b7e>\n    《费用结算服务协议》（以下称“本协议”）为广州科奥信息技术有限公司、海南自贸区椰云网络科技有限公司与您签订。本协议内容包括协议正文及所有广州科奥信息技术有限公司、海南自贸区椰云网络科技有限公司已经发布的或将来可能发布的各类规则。所有规则为本协议不可分割的一部分，与协议正文具有同等法律效力。您通过页面勾选或点击确认或以其他方式选择接受本协议，即表示您与广州科奥信息技术有限公司、海南自贸区椰云网络科技有限公司达成协议并同意接受本协议的全部约定内容以及本协议有关的各项规则，所包含的其他与本协议项下各项规则有关的条款和规定。如果您不同意本协议的任意内容，或者无法准确理解广州科奥信息技术有限公司、海南自贸区椰云网络有限公司对条款的解释，请您停止使用本服务。\n  </p> <p data-v-03f06b7e><b data-v-03f06b7e>\n      在接受本协议之前，请您仔细阅读本协议的全部内容，特别是加粗显示的部分，以及与您权益具有或者有可能具有重大关系的条款，及对公司具有免责或限制责任的条款。\n    </b></p> <h3 data-v-03f06b7e>\n    第一条 协议术语的定义及解释\n  </h3> <p data-v-03f06b7e>\n    广州科奥信息技术有限公司（以下简称“公司”或“甲方”）是根据中华人民共和国相关法律合法设立并有效存续的独立法人，同时完整地享有法定的民事权利能力和民事行为能力。 \n  </p> <p data-v-03f06b7e>\n    您（乙方）是享有完全民事行为能力的自然人，了解本公司平台功能，足以使其能行使本协议项下权利和履行期于本协议项下之义务。乙方是甲方签约的具有专业素养、能力及相应许可的自由职业者，能满足甲方的商业需求。\n  </p> <p data-v-03f06b7e>\n    海南自贸区椰云网络科技有限公司（以下简称“丙方”或“服务公司”）是在中国境内注册成立的能为诸多客户（公司或自由职业者）提供包括费用结算在内的数字经济综合服务的有限公司。成为本公司委托的合作伙伴，依据本协议为您提供服务，本协议对您、本公司和服务公司均具有法律约束力。\n  </p> <h3 data-v-03f06b7e>第二条 费用结算及支付方式</h3> <p data-v-03f06b7e>您按照与甲方签署的服务合同，通过为甲方或甲方用户提供服务获取收益。甲方基于您的服务成果将在您费用核算时，通过丙方向您提供费用结算支付。服务结算金额以本公司平台后台统计数据为准。同时，丙方公司将对应费用向您支付，支付时您应服从丙方公司费用支付政策。</p> <p data-v-03f06b7e>\n    费用的结算周期不作明确约定，由乙方在甲方提供的结算管理后台中发起申请，经过甲方审核后，支付至您提供的提现申请单上列示的收款账户中。因丙方原因造成甲方未能按时支付的，您无权要求甲方负责。\n  </p> <p data-v-03f06b7e>\n    您应自行负担就其取得的绩效费需缴纳的各项税款及行政收费（如有）。丙方接受乙方委托在向其支付其费用时，应当按照有关税收政策规定及乙方主管税务机关的要求，向其代征个人所得税等税款及行政收费（如适用），该等款项将从甲方支付给丙方，并由丙方接受甲方委托在支付给你的费用时予以扣减。应丙方要求，您应提供与税款申报缴纳相关的协助及信息。\n  </p> <p data-v-03f06b7e>\n    各方均清楚了解本协议支付过程的相对性，即您与丙方的费用支付与本公司无关，本公司与丙方公司的费用支付与您无关。\n  </p> <h3 data-v-03f06b7e>第三条 承诺与保证</h3> <div data-v-03f06b7e>\n    （一）甲方承诺与保证\n  </div> <p data-v-03f06b7e>\n    甲方承诺对您所披露的涉及个人隐私的信息进行保密并履行安全保护义务，保障网络免受干扰、破坏或未经授权的访问，防止网络数据泄露或被窃取、篡改。\n  </p> <div data-v-03f06b7e>\n    （二）乙方承诺与保证\n  </div> <p data-v-03f06b7e>\n    您保证其具有完全民事行为能力，满足所有履行本协议所需的法定或约定条件。保证向甲方提供的信息是真实、有效、合法的，如信息变更及时通知甲方。您有权且应当拒绝违反法律法规的服务内容。\n  </p> <div data-v-03f06b7e>\n    （三）丙方承诺与保证\n  </div> <p data-v-03f06b7e>\n    丙方应当本着甲方利益最大化的原则，勤勉履行本协议，维护甲方形象，不得损害甲方的合法权益。丙方承诺甲乙方所披露的个人隐私信息进行保密。\n  </p> <h3 data-v-03f06b7e>第四条 特别约定</h3> <p data-v-03f06b7e><b data-v-03f06b7e>\n      1、您同意，您不可撤销地授权公司和丙方有权为提供前述费用结算服务的需要获取您的相关信息（包括但不限于自然人姓名、身份证号码、收款银行卡号、开户银行、手机号码等）。若您提供任何错错、虚假、失效或不完整的资料，公司和丙方有权暂停或终止向您提供部份或全部服务，对此公司和丙方不承担任何责任。您承诺并同意负担因此所产生的所有损失。若因国家法律法规、部门规章或监管机构的要求，公司和丙方需要您补充提供任何相关资料时，如您不能及时配合提供，公司和丙方有权暂停或终止向您提供部分或全部服务。\n    </b></p> <p data-v-03f06b7e>\n    2、您在向甲方提供服务时所需要的设备设施及生产经营活动的必要费用由您自行承担；因生产经营活动需缴纳的各项税款及行政收费也由您自行承担，并对外独立承担民事责任，不因甲方向您提供数字经济智能综合服务而导致甲乙双方构成任何劳动/劳务合同关系或其他类似劳动法律关系。甲方没有为您购买任何保险的法律义务，您自行购买商业保险的，其投保、出险及理赔事宜均与甲方无关。\n  </p> <p data-v-03f06b7e>\n    3、如甲方因设备或通讯及其他客观原因造成账务差错时 ,甲方有权单方面进行账务更正。乙方应给与积极配合。\n  </p> <p data-v-03f06b7e>\n    4、您确认知晓最终支付您的费用结算方式，可自行在相关页面或合同处查询费用的计算方式、支付途径等信息，并有权向甲方要求支付绩效费。您如有异议，可直接与甲方沟通，甲方视情况需要可提供相关协助。\n  </p> <h3 data-v-03f06b7e>第五条 保密责任</h3> <p data-v-03f06b7e>\n    1、您应严格遵守公司的保密制度，承诺无限期保守公司及/或本平台的商业秘密。因您违反约定使用或披露公司商业秘密和信息使公司遭受任何名誉、声誉或经济上的、直接或间接的损失，您应赔偿公司损失。\n  </p> <p data-v-03f06b7e>\n    商业秘密是指由公司提供的、或者您在双方合作期间了解到的、或者公司对第三方承担保密义务的，与公司业务有关的，能为公司带来经济利益，具有实用性的、非公知的所有信息，包括（但不限于）：技术信息、经营信息和与公司行政管理有关的信息和文件（含本协议及相关协议内容）、您从公司获得的服务费用的金额和结算方式、标准、权利归属方式、授权方式、客户名单、合作伙伴的名单、联系方式、服务费用、公司工作人员名单等不为公众所知悉的信息。\n  </p> <p data-v-03f06b7e>\n    2、您应严格遵守本协议，未经公司书面授权或同意，对公司的商业秘密不得：\n  </p> <p data-v-03f06b7e>\n    (1)以任何方式向第三方或不特定的公众进行传播、泄露；\n  </p> <p data-v-03f06b7e>\n    (2)为非本协议的目的而使用公司的商业秘密。\n  </p> <p data-v-03f06b7e>3、如果本协议因任何原因终止或不再履行，您不得将任何有关公司内容的信息披露或透露给任何本协议之外的其他方。</p> <p data-v-03f06b7e>4、以上保密条款并不因本协议的解除、终止、无效而失效。</p> <h3 data-v-03f06b7e>第六条 免责条款</h3> <p data-v-03f06b7e>您同意，在下列情形下公司、丙方无需承担任何责任：</p> <p data-v-03f06b7e>1、 任何由于黑客攻击、计算机病毒侵入或发作、电信部门技术调整导致之影响、因政府管制而造成的暂时性关闭、由于第三方原因（包括不可抗力，例如国际出口的主干线路及国际出口电信提供商一方出现故障、火灾、水灾、雷击、地震、洪水、台风、龙卷风、火山爆发、瘟疫和传染病流行、罢工、战争或暴力行为或类似事件等）及其他非因公司和服务公司过错而造成的费用结算失败等。</p> <p data-v-03f06b7e>2、您向公司、丙方提供错误、不完整、不实信息等造成费用结算失败或遭受任何其他损失，概与公司、服务公司无关。</p> <h3 data-v-03f06b7e>第七条 其他</h3> <p data-v-03f06b7e>您同意，在下列情形下公司、服务公司无需承担任何责任：</p> <p data-v-03f06b7e>1、因本协议的订立和履行，本平台需要向您发出的任何通知、指示、催告、文件扫描件或电子文档或者其他任何形式的意思表示，均可通过包括但不限于电子邮件、站内信、系统通知或页面公告等形式中的一种或几种发送或接收。</p> <p data-v-03f06b7e>2、本协议的订立、效力、解释、履行和争议解决均适用中华人民共和国法律。</p> <p data-v-03f06b7e>3、执行本协议中发生纠纷，由各方协商解决，协商不成的向广州市越秀区人民法院诉讼解决。</p>")])}
